// src/app/components/ParticleCursor.tsx
"use client";

import { useEffect } from 'react';
import { particlesCursor } from 'threejs-toys';

const ParticleCursor = () => {
  useEffect(() => {
    // Initialize the particlesCursor
    const pc = particlesCursor({
      el: document.body, // Track mouse movements over the whole body
      gpgpuSize: 512,
      colors: [0x00fffc, 0x0000ff],
      pointSize: 2,
      pointDecay: 0.0025,
      sleepRadiusX: 250,
      sleepRadiusY: 250,
    });

    // Cleanup on unmount
    return () => {
      if (pc && typeof pc.destroy === 'function') {
        pc.destroy();
      }
    };
  }, []);

  return <div className="particle-cursor" />; // Render an empty div for positioning
};

export default ParticleCursor;
